import { ShaderPlugin } from "./plugins/ShaderPlugin";
import { DoricStackViewNode } from "./shader/DoricStackViewNode";
const bundles = new Map;
const plugins = new Map;
const nodes = new Map;
export function acquireJSBundle(name) {
    return bundles.get(name);
}
export function registerJSBundle(name, bundle) {
    bundles.set(name, bundle);
}
export function registerPlugin(name, plugin) {
    plugins.set(name, plugin);
}
export function acquirePlugin(name) {
    return plugins.get(name);
}
export function registerViewNode(name, node) {
    nodes.set(name, node);
}
export function acquireViewNode(name) {
    return nodes.get(name);
}
registerPlugin('shader', ShaderPlugin);
registerViewNode('Stack', DoricStackViewNode);
